* {
    box-sizing: border-box;
    font-family: "JetBrains Mono", monospace;
  }
  
  body {
    background-color: black;
    text-decoration-color: white;
    /* Hide scrollbar for IE, Edge and Firefox */
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }
  
  /* Hide scrollbar for Chrome, Safari and Opera */
  body::-webkit-scrollbar {
    display: none;
  }
  
  .container {
    display: grid;
    place-items: center;
    margin: 20vh 20vw;
    padding: 5px;
  }
  
  .header {
    max-width: 650px;
    font-size: xx-large;
    color: white;
    text-align: justify;
  }
  
  .subtitle {
    max-width: 650px;
    font-size: x-large;
    color: grey;
    text-align: justify;
    padding-top: 5px;
  }
  
  .links {
    color: grey;
    font-size: medium;
    text-decoration: none;
    padding-top: 50px;
    text-align: justify;
  }
  
  a {
    text-decoration: none;
  }
  
  .header {
    max-width: 650px;
    font-size: xx-large;
    color: white;
    text-align: justify;
  }
  
  .about {
    font-size: medium;
    color: white;
    max-width: 600px;
    padding-top: 40px;
    font-size: large;
    line-height: 1.5;
    text-align: justify;
  }
  
  .hover-underline-animation {
    display: inline-block;
    position: relative;
    color: #93e9be;
    font-size: large;
  }
  
  .hover-underline-animation:after {
    content: "";
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #93e9be;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
  }
  
  .hover-underline-animation:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }
  
  @media screen and (max-width: 600px), screen and (max-height: 600px) {
    .container {
      margin: 15vh auto;
    }
  }